.double-bounce {
  width: 40px;
  height: 40px;
  position: relative;
  margin: 40px auto;
}
.double-bounce .child {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #333;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
